*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  display: block;
  width: 100%;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: var(--light-color);

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.img-radius.overlay {
  position: relative;

  &::after {
    content: "";
    border-radius: 50%;
  }
}

.overlay,
.img-radius.overlay {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../img/hero-bg-overlay.png');
    background-position: center bottom;
    background-size: cover;
    background-repeat: no-repeat;
    mix-blend-mode: color-burn;
  }
}

.slider-btns {
  position: absolute;
  right: 10px;
  top: 10px;
  display: none;
  align-items: center;
  justify-content: space-between;
  width: 155px;

  @include small-tablet {
    left: 50%;
    right: inherit;
    top: 100%;
    transform: translateX(-50%);
  }

  @include big-mobile {
    width: 128px;
  }

  &__prev,
  &__next  {
    cursor: pointer;

    svg {
      fill: var(--text-color);
      transition: fill .3s ease-in-out;

      @include hover {
        fill: var(--yel-color);
      }

      @include big-mobile {
        width: 36px;
      }
    }
  }

  &__counter {
    position: absolute;
    left: 50%;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: var(--text-color);
    transform: translateX(-50%);
  }

  &__next {
    cursor: pointer;
  }
}

.disabled svg {
  fill: #D6D6D6;
  pointer-events: none;
}
