.tournament {
  padding: 80px 0 90px;

  @include big-mobile {
    padding: 50px 0 60px;
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 44px;

    &:not(:last-child) {
      margin-bottom: 44px;
    }

    &:last-child {
      .tournament__img {
        @include big-mobile {
          width: 110%;
        }
      }
    }

    @include tablet {
      flex-wrap: wrap;
    }

    @include big-mobile {
      gap: 24px;
    }
  }

  &__row--gap {
    gap: 5px;

    @include tablet {
      gap: 24px;
    }
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 36px;
    line-height: 135%;
    text-transform: uppercase;
    color: var(--text-color);

    @include tablet {
      display: contents;
    }

    @include big-mobile {
      font-size: 28px;
    }
  }

  &__title--mb {
    margin-bottom: 60px;
  }

  &__title-color {
    font-weight: 700;
    letter-spacing: 0.5px;
    color: var(--bg-ticker);
  }

  &__order {
    @include tablet {
      order: 1;
    }
  }

  &__img {
    flex-shrink: 0;

    @include tablet {
      margin: 0 auto;
    }

    @include big-mobile {
      width: 100%;
    }
  }

  &__img--transform {
    margin-left: -16px;
    transform: translate(-12px, -6px);

    @include big-mobile {
      margin-left: 0;
      transform: translate(-17px, -4px);
    }
  }

  &__table {
    margin-bottom: 30px;

    @include tablet {
      margin-top: 44px;
    }

    @include big-mobile {
      margin-bottom: 22px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--blue-color);

    @include big-mobile {
      font-size: 18px;
    }
  }
}

.img-radius {
  border-radius: 50%;
}

.table {
  border: none;
  border-collapse: collapse;
  width: 100%;

  &__row {
    @include big-mobile {
      display: block;
      width: 100%;
    }

    &:not(:last-child) {
      border-bottom: 1px solid var(--alto-color);
    }

    &:nth-child(4) {
      @include big-mobile {
        display: inline-table;
      }

      .table__header {
        @include big-mobile {
          display: inline;
          width: 47%;
        }
      }

      .table__text {
        @include big-mobile {
          display: inline-block;
          padding-top: 16px;
        }
      }
    }
  }

  &__header {
    border-right: 1px solid var(--alto-color);
    padding: 16px 16px 16px 0;
    width: 306px;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    text-align: left;
    color: var(--text-color);
    opacity: 0.7;

    @include big-mobile {
      display: block;
      border-right: none;
      padding-bottom: 12px;
      width: 100%
    }
  }

  &__text {
    padding: 16px;
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-color);

    @include big-mobile {
      padding-top: 0;
      padding-left: 0;
    }
  }

  &__decore {
    margin-right: 12px;
    text-decoration-color: var(--bg-ticker);
  }
}

.pt-0 {
  padding-top: 0;
}
