.hero {
  position: relative;
  height: 100%;
  min-height: 700px;
  background-color: var(--gray-color);

  @include big-mobile {
    min-height: 773px;
  }

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: calc(100% - 810px);
    width: 100%;
    height: 860px;
    max-width: 1628px;
    background-image: url('../../img/hero-bg.png');
    background-position: 45% 100%;
    background-size: cover;
    background-repeat: no-repeat;
    transform: translateX(-50%);

    @include big-mobile {
      top: inherit;
      bottom: -20px;
      height: 410px;
      max-width: 887px;
      background-image: url('../../img/hero-bgm.png');
      background-position: 79% 100%;
    }
  }

  &__content {
    position: relative;
    z-index: 1;
    padding-top: 124px;
    width: 100%;
    max-width: 542px;

    @include big-mobile {
      padding-top: 91px;
      max-width: 100%;
    }
  }

  &__title-center {
    display: inline-block;
    width: 100%;
    text-align: center;
  }

  &__title-right {
    display: inline-block;
    width: 100%;
    text-align: right;
  }

  &__text {
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    text-align: center;
    color: var(--text-color);

    @include big-mobile {
      margin-bottom: 30px;
      font-size: 18px;
      text-align: left;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;

    @include big-mobile {
      flex-direction: column;
    }
  }

  &__btn {
    flex-basis: 50%;
    border-radius: 66px;
    padding: 14px 46px;
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    text-align: center;

    @include big-mobile {
      padding: 11px;
      width: 100%;
    }
  }

  &__btn--black {
    border: 2px solid transparent;
    color: var(--light-color);
    background-color: var(--mine-color);
    transition: background-color .3s ease-in-out, color .3s ease-in-out ;

    @include hover {
      color: var(--mine-color);
      background-color: var(--yel-color);
    }
  }

  &__btn--light {
    border: 2px solid var(--mine-color);
    color: var(--mine-color);
    transition: background-color .3s ease-in-out, color .3s ease-in-out ;

    @include hover {
      color: var(--light-color);
      background-color: var(--mine-color);
    }

    @include tablet {
      border-color: transparent;
      background-color: rgba(255, 255, 255, 0.9);
    }
  }
}

.title {
  margin: 0;
  margin-bottom: 24px;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 60px;
  line-height: 110%;
  text-transform: uppercase;
  color: var(--text-color);

  @include big-mobile {
    font-size: 36px;
  }
}
