.ticker {
  position: relative;
  overflow: hidden;
  padding: 18px 0;
  background: var(--bg-ticker);

  @include big-mobile {
    padding: 10px 0;
  }

  &__wrapper {
    display: flex;
  }

  &__item {
    flex-shrink: 0;
    white-space: nowrap;
    animation: ticker 25s linear infinite;
  }

  &__text {
    position: relative;
    padding: 0 12px 0 18px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 110%;
    text-transform: uppercase;
    color: var(--light-color);

    @include big-mobile {
      font-size: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      display: block;
      border-radius: 50%;
      width: 6px;
      height: 6px;
      background-color: var(--light-color);
      transform: translateY(-50%);
    }
  }
}

@keyframes ticker {
  0% {
    transform: translateZ(0);
  }

  100% {
    transform: translate3d(-100%, 0, 0);
  }
}
