.section-title {
  margin: 0;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 54px;
  line-height: 120%;
  text-transform: uppercase;
  color: var(--text-color);

  @include big-mobile {
    font-size: 36px;
    line-height: 110%;
  }
}

.stages {
  padding: 90px 0;

  @include big-mobile {
    padding-top: 60px;
  }

  &__title {
    margin-bottom: 50px;
    width: 100%;
    max-width: 800px;

    @include big-mobile {
      margin-bottom: 145px;
    }
  }

  &__subtitle {
    display: inline-block;
    margin-left: 5px;
    width: 100%;
    max-width: 280px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    text-transform: none;
    color: var(--blue-color);

    @include big-mobile {
      margin-top: 14px;
      margin-left: 0;
      max-width: 100%;
      font-size: 18px;
    }
  }

  &__list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 20px;

    @include big-mobile {
      grid-template-columns: repeat(5, 100%);
      row-gap: 0;
    }
  }

  &__item {
    counter-increment: stage 1;
    position: relative;
    grid-column: 2 span;
    padding: 20px 20px 20px 72px;

    @include tablet {
      grid-column: 3 span;
    }

    @include big-mobile {
      grid-column: auto;
      padding-top: 10px;
    }

    &:not(:nth-child(2), :nth-child(5)) {
      @include big-mobile {
        padding-top: 62px;
      }
    }

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background-image: url('../../img/stagesitem-bg.jpg');
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: translateX(-50%);
    }

    &:nth-child(2) {
      @include big-mobile {
        grid-column-start: 1;
        grid-row-start: 2;
        padding-bottom: 35px;
      }
    }

    &:nth-child(3) {
      grid-column: 5 / 7;
      grid-row: 1 / 3;

      @include tablet {
        grid-column: 3 span;
        grid-row: auto;
      }

      @include big-mobile {
        grid-column: auto;
        grid-row: span 2;

      }
    }

    &:nth-child(5) {
      padding-right: 70px;

      @include big-mobile {
        grid-column-start: 3;
        grid-row-start: 2;
      }
    }

    &:nth-child(6) {
      @include big-mobile {
        grid-row: span 2;
      }
    }

    &:last-child {
      grid-column: 3/7;

      @include tablet {
        grid-column: 6 span;
      }

      @include big-mobile {
        grid-column: auto;
        grid-row: span 2;
      }

      .stages__text {
        padding-right: 300px;

        @include big-mobile {
          padding-right: 0;
        }
      }
    }
  }

  &__text {
    position: relative;
    z-index: 1;
    display: inline-block;
    font-weight: 500;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-color);

    @include big-mobile {
      font-size: 18px;
    }

    &::before {
      content: counter(stage);
      position: absolute;
      left: -50px;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 60px;
      width: 36px;
      height: 36px;
      background-color: var(--light-color);
    }
  }

  &__slider-wrapper {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      right: -60px;
      bottom: 35px;
      z-index: 1;
      display: block;
      width: 390px;
      height: 200px;
      background-image: url("../../img/photo-3.png");
      background-size: cover;
      background-repeat: no-repeat;

      @include tablet {
        bottom: -20px;
      }

      @include big-mobile {
        left: 50%;
        right: inherit;
        top: -134px;
        bottom: inherit;
        width: 320px;
        height: 200px;
        transform: translateX(-50%) rotate(-4deg);
      }
    }
  }

  &__btns {
    display: none;

    @include big-mobile {
      bottom: -95px;
      display: flex;
      width: 80px;

    }
  }

  &__nav {
    position: absolute;

    &--prev {
      left: -50px;
    }

    &--next {
      right: -50px;
    }
  }

  &__dots {
    display: flex;
    align-items: center;
    justify-content: center;

    .stages__dot {
      margin: 0 3px;
      border-radius: 50%;
      width: 10px;
      height: 10px;
      background-color: var(--dots-color);
      cursor: pointer;
    }

    .stages__dot.active {
      background-color: var(--text-color);
    }
  }
}
