.participants {
  padding: 115px 0 135px;

  @include big-mobile {
    padding: 90px 0 143px;
  }

  &__container {
    position: relative;
  }

  &__title {
    margin-bottom: 60px;

    @include big-mobile {
      margin-bottom: 38px;
    }
  }

  &__slider {
    overflow: hidden;
    margin: auto;

    @include small-tablet {
      padding-bottom: 40px;
    }

    @include big-mobile {
      padding-bottom: 34px;
    }
  }

  &__slides {
    display: flex;
    transition: transform 0.5s ease-in-out;
    gap: 22px;

    @include big-mobile {
      gap: 0;
    }
  }

  &__slide {
    height: 100%;
    min-width: calc(33.33% - 20px);

    @include tablet {
      min-width: calc(50% - 20px);
    }

    @include big-mobile  {
      min-width: 100%;
    }
  }

  &__pic {
    position: relative;
    display: flex;
    justify-content: center;
    padding-top: 20px;
    background-color: rgba(66, 66, 65, 0.2);

    @include big-mobile {
      padding-top: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background-image: url('../../img/participants-bg.jpg');
      background-position: center center;
      background-size: cover;
      background-repeat: no-repeat;
      transform: translateX(-50%);
    }
  }

  &__img {
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 28px;
    border-radius: 196px;
    width: 310px;
    height: 320px;
    text-align: center;

    @include big-mobile {
      border-radius: 149px;
      width: 238px;
      height: 247px;
    }
  }

  &__content {
    text-align: center;
  }

  &__name {
    margin: 0;
    margin-bottom: 6px;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: var(--codgray-color);
  }

  &__status {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--codgray-color);
    opacity: 0.6;
  }

  &__btns {
    display: flex;
  }

  &__btn {
    display: inline-block;
    border: 1px solid var(--blue-color);
    border-radius: 62px;
    padding: 8px 10px;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: var(--blue-color);
    transition: background-color .3s ease-in-out, color .3s ease-in-out ;

    @include hover {
      color: var(--light-color);
      background-color: var(--blue-color);
    }
  }
}
